import cdstyle from "./style.css";
import loader from "../../assets/header/loader.gif";

const CDFormStyleButton = ({ props, loading }) => {
  let txt = "Check Your Eligibility";
  const getText = () => {
    return txt.split("").map((t, i) => <span key={i}>{t}</span>);
  };

  return (
    <div class={cdstyle.cdStyleContent}>
      <button {...props} class={cdstyle.cdButton} type="submit">
        <div>{getText()}</div>
        {loading && <img src={loader} alt="" className="loader-icon" />}
      </button>
    </div>
  );
};
export default CDFormStyleButton;
